import React from 'react'
import {
    Descriptions,
    Alert
} from 'antd'
import moment from 'moment'
import {
    numberWithCommas
} from '../../../../Utils/math'

type OType = {
    range: {
        start: any,
        end: any
    },
    totalAmount: number
    totalRecords: number
}
const Overview:React.FC<OType>  = ({
    range,
    totalAmount,
    totalRecords
}) => {
    return (
        <Alert
            closable
            type="info"
            message={(
                <Descriptions title="Tng th" column={4}>
                    <Descriptions.Item label="T">{moment(range.start).format('DD/MM/YYYY')}</Descriptions.Item>
                    <Descriptions.Item label="n">{moment(range.end).format('DD/MM/YYYY')}</Descriptions.Item>
                    <Descriptions.Item label="S ln thu/chi">{totalRecords}</Descriptions.Item>
                    <Descriptions.Item label="S Tin">{`${numberWithCommas(totalAmount)} VN`}</Descriptions.Item>
                </Descriptions>
            )}
        />
    )
}

export default Overview