import React from 'react'
import {
    Modal,
    Select,
    Form,
    message,
    List
} from 'antd'
import {
    Link
} from 'react-router-dom'
import hangbangs from '../../../common/HangBang'

const { Option } = Select

type MType = {
    visible: boolean
    onCancel: () => void
    onOk: (ycdh: string) => void
    students: Record<string,any>[]
}

const ChangeGradeModal:React.FC<MType> = ({
    visible,
    onCancel,
    onOk,
    students
}) => {
    const [form] = Form.useForm()
    const [loading, toggleLoading] = React.useState(false)
    return (
        <Modal
            visible={visible}
            onCancel={onCancel}
            title="i hng bng"
            onOk={async() => {
                toggleLoading(true)
                const {hangmoi} = form.getFieldsValue()
                // if(student.hangbang === hangmoi) {
                //     toggleLoading(false)
                //     message.warning(`H s ang  hng ${hangmoi} - khng c g thay i`)
                //     onCancel()
                // }
                await onOk(hangmoi)
                toggleLoading(false)
                onCancel()
            }}
            okButtonProps={{
                loading
            }}
        >
            <p>i hng bng cho hc vin</p>
            <List
                dataSource={students}
                itemLayout="horizontal"
                renderItem={(item: any) => {
                    return (
                        <List.Item key={item._id} actions={[item.hangbang]}>
                            <List.Item.Meta
                                title={<Link to={`/std/${item.randomId}`}>{item.ten}</Link>}
                                description={`M h s: ${item.randomId}`}
                            />
                        </List.Item>
                    )
                }}
            />
            <Form form={form}>
                <Form.Item name="hangmoi" label="Chn hng bng mi">
                    <Select>
                        {Object.keys(hangbangs).map(k => (
                            <Option key={k} value={k}>{`${k} - ${hangbangs[k as keyof Object]}`}</Option>
                        ))}
                    </Select>
                </Form.Item>
            </Form>
        </Modal>
    )
}
export default ChangeGradeModal